/* Dots */

.slick-dots {
    position: absolute;
    bottom: 15px;
    left: 20px;
    list-style: none;
    display: block;
    padding: 0;
    margin: 0;
    // width: 100%;
    li {
        position: relative;
        display: inline-block;
        height: 20px;
        width: 20px;
        margin: 0 5px;
        padding: 0;
        cursor: pointer;
        button {
            border: 1px solid $grey;
            background: white;
            box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.2);
            display: block;
            border-radius: 50%;
            height: 4px;
            width: 4px;
            outline: none;
            line-height: 0px;
            font-size: 0px;
            color: transparent;
            padding: 4px;
            cursor: pointer;
            &:hover, &:focus {
                outline: none;
                &:before {
                    opacity: $slick-opacity-on-hover;
                }
            }
        }
        
    }
    li.slick-active button{
            background: $black;
            opacity: $slick-opacity-default;
        }
}
